/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-quiz-block {
	@apply rounded border border-gray-200;
}

.wp-block-create-block-quiz-block {

	.select-wrapper {
		@apply mb-5;
	}

	.answer-heading {
		@apply flex p-5 text-white justify-between bg-gray-600;

		.answer-text {
			@apply basis-[80%];
		}

		.answer-correction {
			@apply basis-[10%];
		}

		.answer-deletion {
			@apply basis-[10%];
		}
	}

	.answer-list {
		@apply border border-gray-600;

		.answer-option {
			@apply flex items-center p-4 border-b border-gray-600;

			&:last-child {
				@apply border-b-0;
			}

			input[type="text"] {
				@apply block w-full border-none text-base pl-0 #{!important};

				&:focus {
					@apply shadow-none #{!important};
				}
			}
	
			.answer-text {
				@apply basis-[80%];
			}
	
			.answer-correction {
				@apply basis-[10%];
			}

			.answer-deletion {
				@apply text-right basis-[10%];
			}
		}
	}

	.add-answer-button-wrapper {
		@apply mt-5;
	}

}
